import { React, PureComponent } from 'react';

export default class Card extends PureComponent {
  render() {
    return (
    <div className="flex flex-col bg-indigo-100 w-full overflow-hidden mb-2 sm:w-52">
    <img src={this.props.image} alt="" className="bg-white mb-3 h-20 m-6 object-contain"/>
    <h2 className="text-center px-2 pb-5">{this.props.content}</h2>  
    </div>
    );
  }
}
