import { React, PureComponent } from 'react';
import SideBar from './SideBar';

export default class Index extends PureComponent {
  render() {
    return (
      <SideBar content={
        <div className="flex-1 p-10 md:ml-20">
            <span className="material-icons mr-1">face</span> 
            <span className="text-3xl font-bold">About</span>
            <p className="mt-8 mb-10 text-xl">I am an aspiring backend developer who is also exploring other areas in the field.<br/>
            Currently, I am interested and discovering more into the area of cloud computing.</p>
            <span className="material-icons mr-1">dashboard</span>
            <span className="text-3xl font-bold">Skills</span>
            <div className="mt-10">
            <span className="text-2xl font-bold">Languages</span>
            <ul className="list-disc text-xl p-5 mb-10">
                <li>Python</li>
                <li>C</li>
                <li>C#</li>
                <li>Javascript</li>
                <li>HTML/CSS</li>
            </ul>
            <span className="text-2xl font-bold">Technologies</span>
            <ul className="list-disc text-xl p-5">
                <li>ReactJs</li>
                <li>Flask</li>
                <li>SQLite3</li>
                <li>MySQL</li>
            </ul>
        </div>
        </div>
      }></SideBar>
    );
  }
}
